export default{
	data(){
		return{
			myPhone: '',
			myNewPwd: '',
			myNewPwdAgain: '',
			myYzm: '',
			yzmBtnTitle:'获取验证码',
			timestatus:false,
		}
	},
	
	methods:{
		
		sendYzm(){
			if(this.myPhone.length==0){
				uni.showToast({
					icon:'none',
					title:"请输入手机号"
				});
				return;
			}
			
			uni.request({
				url:this.mainServer+'Home/Index/sms_send',
				data:{
					phone:this.myPhone,
					type:3
				},
				method:"POST",
				success: (res) => {
					console.log(res);
					uni.showToast({
						icon:"none",
						title:res.data.message
					});
					this.yzmBtnTitle=60;
					 this.timestatus = false
					       var interval = setInterval(() => {
					          --this.yzmBtnTitle
					       }, 1000)
					       setTimeout(() => {
					          clearInterval(interval)
					          this.timestatus = true;
							  this.yzmBtnTitle='重新获取';
					       }, 60000)
					
				}
			})
			
			
			
		},
		
		clickLogin(){
			
			if(this.myPhone.length==0){
				uni.showToast({
					icon:"none",
					title:"请输入手机号"
				});
				return;
			}
			if(this.myPwd.length==0){
				uni.showToast({
					icon:'none',
					title:"请输入帐号密码"
				});
				return;
			}
			if(this.myYZM.length==0){
				uni.showToast({
					icon:'none',
					title:"请输入验证码"
				});
				return;
			}
			
			
			
			uni.request({
				url:this.mainServer+'Home/Index/forget',
				data:{
					phone:this.myPhone,
					code:this.myYZM,
					password:this.myPwd,
					repass:this.myPwdAgain,
				},
				method:"POST",
				success: (res) => {
					console.log(res);
					uni.showToast({
						icon:"none",
						title:res.data.message
					});
					// 本地存入 同步
					if(res.data.code==1){
						
						uni.navigateBack({
							
						});
						
					}
				}
			})
		},
		
	}
}
